@import "../App.scss";

.dashboard {
  position: relative;

  .dashboard__navbar {
    background-color: white;
    box-shadow: 1px 0px 5px rgba(0, 0, 0, 0.1);

    position: relative;
    left: 0;
    right: 0;
    z-index: 999;

    @media #{$desktop} {
      position: fixed;
    }

    .dashboard__navbar-wrapper {
      max-width: 1920px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 auto;
      width: 100%;
      padding: 1rem 1rem;

      @media #{$tablet} {
        padding: 0.5rem 2rem;
      }
    }

    .dashboard__navbar-right {
      display: flex;
      align-items: center;
      height: 100%;

      .search {
        width: 300px;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        top: 100px;
        margin: 0 auto;
        width: 95%;

        @media #{$desktop} {
          position: relative;
          left: unset;
          top: unset;
          transform: none;
          width: 500px;
        }

        .search-input {
          padding: 0.5rem 3rem 0.5rem 1rem;
          border: none;
          background-color: #f8f8fa;
          width: 100%;
          border-radius: 10px;

          &:focus {
            outline: none;
          }
        }

        .search-button {
          border: none;
          background-color: transparent;
          position: absolute;
          right: 1rem;
          top: 50%;
          transform: translateY(-50%);
          outline: none;
        }
      }

      .person-icon {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $red;
        color: white;
        margin-right: 0.5rem;
        margin-left: 1rem;
        margin-bottom: 0 !important;
        display: none;
        @media #{$tablet} {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
      h2 {
        margin-bottom: 0 !important;
        font-size: 1rem;
        display: none;
        @media #{$tablet} {
          display: block;
        }
      }
    }
  }

  .dashboard__alert {
    position: fixed;
    z-index: 9;
    top: calc(100vh - 100px);
    left: 1rem;
    right: 1rem;

    @media #{$tablet} {
      left: 2rem;
      right: 2rem;
      top: 70px;
    }
  }

  .dashboard__main {
    width: 100%;
    margin: 0 auto;

    max-width: 1920px;
    padding: 8rem 1rem;

    @media #{$desktop} {
      padding: 8rem 2rem;
    }

    .projects-filter {
      .projects-filter-item {
        position: relative;
        font-size: 1.2rem;
        margin: 0 2rem;
        padding: 0;
        color: #a1a1a1;
        transition: 0.3s;
        user-select: none;
        cursor: pointer;
        &.active {
          color: black;
          border-bottom: 2px solid $red;
        }

        span {
          text-align: center;
          position: absolute;
          font-size: 13px;
          background-color: $red;
          min-width: 20px;
          height: 20px;
          color: white;
          right: -20px;
          top: -10px;
          border-radius: 50%;
          line-height: 20px;
          font-weight: bold;
        }
      }
    }

    .dashboard__main-grid {
      width: 100%;
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;

      @media #{$tablet} {
        grid-template-columns: 1fr 1fr;
      }

      @media #{$desktop} {
        grid-template-columns: repeat(3, 1fr);
        gap: 40px;
      }

      @media #{$large-desktop} {
        grid-template-columns: repeat(4, 1fr);
      }

      .new-project {
        display: flex;
        flex-direction: column;
        justify-content: center;
        min-width: 200px;
        user-select: none;

        &:hover {
          background-color: $red;
          color: white;
        }
      }

      .grid-item {
        cursor: pointer;
        min-height: 300px;
        background-color: white;
        box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1);
      }

      .project.grid-item {
        padding: 1rem;
        position: relative;
        overflow: hidden;

        &:hover {
          .actions-box {
            opacity: 1;
            transform: translateY(0);
          }
        }
        .ribbon-wrapper {
          width: 88px;
          height: 88px;
          overflow: hidden;
          position: absolute;
          top: -3px;
          right: -3px;
          z-index: 1;

          .ribbon-tag {
            text-align: center;
            -webkit-transform: rotate(45deg);
            -moz-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            -o-transform: rotate(45deg);
            position: relative;
            padding: 5px 0;
            left: -4px;
            top: 15px;
            width: 120px;
            color: #ffffff;
            background: #707070;
            font-weight: bold;

            &.public {
              background-color: rgb(0, 147, 245);
            }
          }
        }
        .project-image {
          background-color: $red;
          height: 250px;
          width: 100%;

          overflow: hidden;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }

        .actions-box {
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          right: 0;
          background-color: rgba(240, 28, 74, 0.9);
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          transform: translateY(calc(100% - 5px));
          transition: 0.5s;

          .actions-item {
            color: black;
            border-radius: 100%;
            width: 60px;
            height: 60px;
            min-width: 60px;
            min-height: 60px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: 0.3s;
            color: white;
            border: 2px solid white;

            &:hover {
              background-color: rgb(255, 255, 255);
              color: $red;
              .github-logo {
                fill: $red;
              }
            }

            .github-logo {
              fill: white;
            }
          }
        }
      }
    }
  }
}
