.required:after {
  content: ' *';
  color: red;
}

.project-image-preview {
  width: 200px;
}

.projectForm___technologies-list {
  .projectForm___technologies-item {
    position: relative;
    animation: zoomIn 1s ease;

    .btn-remove-technology {
      background: rgba(0, 0, 0, 0.5);
      color: white;
      border-radius: 50%;
      position: absolute;
      top: -10px;
      right: -18px;
      font-size: 16px;
      width: 20px;
      height: 20px;
      line-height: 16px;
      text-align: center;
      cursor: pointer;
      user-select: none;

      &:hover {
        background-color: black;
      }
    }
  }
}
